import { makeElement, autocompleteInput, makeDeleteAction, makeMoreActions } from '../templates/widget_generation_tools';

export const itemsContents = function itemsContents (widgetName, widgetId, icon, itemsSrc = '') {
  const container = makeElement('div', [], {}, '')

  const contents = makeElement('div', [], {
    'data-kubik-widget-items-target': 'itemsTabContainer'
  })

  const itemTemplateContent = makeElement('div', ['kubik-wysiwyg-component--item-fields'], {})

  const itemTemplate = makeElement('template', [], {
    id: [widgetId, 'template'].join('-'),
    'data-kubik-widget-items-target': 'itemTemplate'
  })

  const itemStaticTemplate = makeElement('template', [], {
    id: [widgetId, 'static-template'].join('-'),
    'data-kubik-widget-items-target': 'itemStaticTemplate'
  })

  const itemStaticContent = makeElement('div', ['kubik--item-static-content'], {})

  const itemThumbContainer = makeElement('div', ['kubik--item-static-content--thumb'], {
    'data-kubik-item-name': 'thumb'
  })
  itemThumbContainer.insertAdjacentHTML('beforeend', icon)

  const itemMainInfoContainer = makeElement('div', ['kubik--item-static-content--main-info'], {
    'data-kubik-item-name': 'display_name'
  })
  const itemAdditionalInfoContainer = makeElement('div', ['kubik--item-static-content--additional-info'], {
    'data-kubik-item-name': 'additional_info'
  })
  const itemStatusInfoContainer = makeElement('div', ['kubik--item-static-content--status-info'], {
    'data-kubik-item-name': 'status_info'
  })
  itemStaticContent.appendChild(itemThumbContainer)
  itemStaticContent.appendChild(itemMainInfoContainer)
  itemStaticContent.appendChild(itemAdditionalInfoContainer)
  itemStaticContent.appendChild(itemStatusInfoContainer)

  const itemStaticTemplateContent = makeElement('div', ['kubik--item'], {

    'data-controller': 'kubik_item',
  })

  const itemsContainer = makeElement('div', [], {
    'data-kubik-widget-items-target': 'itemsContainer'
  })
  const deleteContainer = makeElement('div', ['kubik-wysiwyg-component--items-action-container'], {
  })
  deleteContainer.append(makeDeleteAction())

  const addButton = makeElement('div', ['kubik-wysiwyg-component--add-item'],
    {
      'data-kubik-widget-items-target': 'addButton',
      'data-action': 'click->kubik-widget-items#addItem'
    },
    'Add author'
  )

  const authorSelector = autocompleteInput(
    {
      fieldName: 'author',
      widgetId: widgetId,
      fieldLabel: 'Author',
      fieldHint: 'Select saved author',
      itemsSrc: itemsSrc,
    }
  )

  itemTemplate.appendChild(authorSelector)

  contents.appendChild(itemsContainer)
  contents.appendChild(addButton)

  itemTemplateContent.appendChild(authorSelector)

  itemTemplateContent.appendChild(makeMoreActions())

  itemTemplate.content.appendChild(itemTemplateContent)

  itemStaticTemplate.content.appendChild(deleteContainer)
  itemStaticTemplate.content.appendChild(itemStaticContent)
  itemStaticTemplate.content.appendChild(makeMoreActions())


  container.appendChild(itemTemplate)
  container.appendChild(itemStaticTemplate)
  container.appendChild(contents)

  return container
}
