import { Controller } from "@hotwired/stimulus"

export default class extends Controller {

  settingsValue: Object
  tabContainerTarget: HTMLElement

  static targets = ['settingsTabContainer']

  static values = {
    settings: Object
  }

  connect():void {
    console.log('connected settings')
  }

  updateField(e:Event):void {
    const target = e.target as HTMLInputElement
    const value = target.value
    const fieldName = target.dataset.kubikName
    this.settingsValue = Object.assign(this.settingsValue, { [fieldName]: value })
  }
}
