import { Controller } from "@hotwired/stimulus"
import { makeDeleteAction } from '../templates/widget_generation_tools';

export default class extends Controller {
  itemValue: any
  itemIdValue: number

  widgetIdValue: string
  urlValue: string
  itemIndexValue: number
  itemEditingValue: boolean

  static targets = ['widgetContainer']
  static values = {
    itemIndex: Number,
    item: Object,
    itemId: Number,
    url: String,
    itemEditingValue: { type: Boolean, default: true },
    widgetId: String,
  }

  connect():void {
    this._fetchUpdatedInfo()
    this._render()
  }

  fields():Array<string> {
    return ['author']
  }

  itemValueChanged():void {
    this.itemEditingValue = false
    console.log('changed: ', this.itemValue)
    this._renderItem()
  }

  fieldMapping():Object {
    const additional_info = this.itemValue.additional_information ?
      typeof this.itemValue.additional_information === 'object' ?
        this.itemValue.additional_information :
        JSON.parse(this.itemValue.additional_information) :
      {}
    return {
      author: ''
    }
  }

  _fetchUpdatedInfo():void {
    if(this.urlValue != '') {
      fetch(this.urlValue)
      .then((response) => response.json())
      .then((data) => this.itemValue = data.return_object);
    }
  }

  update_index(templateClone):HTMLTemplateElement {
    const itemsToIndex = templateClone.content.querySelectorAll('[data-item-index=""]') as NodeListOf<HTMLElement>
    itemsToIndex.forEach((item) => {
      item.dataset.itemIndex = this.itemIndexValue.toString()
    })
    return templateClone
  }

  update_fields_values(templateClone):HTMLTemplateElement {
    const values = this.itemValue

    this.fields().forEach((field) => {
      const f = templateClone.content.querySelector(`[data-kubik-name="${field}"]`) as HTMLInputElement

      const value = this.fieldMapping()[field]

      f.value = value
    })
    return templateClone
  }

  update_item_values(templateClone):HTMLTemplateElement {
    const values = this.itemValue
    for(const key in values) {
      const f = templateClone.content.querySelector(`[data-kubik-item-name="${key}"]`) as HTMLElement
      if(f && values[key] && (values[key].length > 0 || Object.entries(values[key]).length > 0)) {
        switch(key) {
          case 'thumb':
            f.innerHTML = ''
            const thumb = document.createElement('img')
            thumb.src = values[key]
            f.appendChild(thumb)
            break;
          case 'status_info':
            f.innerHTML = ''
            for (const [k, v] of Object.entries(values[key])) {
              const pill = document.createElement('span')
              pill.classList.add('kubik-pill', `kubik-pill--${k}`)
              pill.innerHTML = v as string;
              f.appendChild(pill)
            }
            break;
          default:
            f.innerHTML = values[key]
            break;
        }
      }
    }
    return templateClone
  }

  _renderPlaceholder(): void {
  }


  _renderForm(): void {
    const template = document.getElementById([this.widgetIdValue, 'template'].join('-')) as HTMLTemplateElement

    let templateClone = template.cloneNode(true) as HTMLTemplateElement

    templateClone = this.update_index(templateClone)
    templateClone = this.update_fields_values(templateClone)

    const thumbField = templateClone.content.getElementById([this.widgetIdValue.toString(), 'image'].join('-')) as HTMLInputElement

    this.element.appendChild(templateClone.content)
  }

  _renderItem(): void {
    const template = document.getElementById([this.widgetIdValue, 'static-template'].join('-')) as HTMLTemplateElement
    let templateClone = template.cloneNode(true) as HTMLTemplateElement
    templateClone = this.update_index(templateClone)
    templateClone = this.update_item_values(templateClone)
    this.element.innerHTML = ''
    this.element.appendChild(templateClone.content)
  }

  _render(): void {
    this.element.innerHTML = ''
    if(!(typeof(this.itemValue.id) == 'undefined')) {
      this._renderItem()
    } else if(this.itemEditingValue = true && this.itemValue.id) {
      this._renderForm()
    } else {
      this._renderForm()
    }
  }
}
