import { Controller } from "@hotwired/stimulus"

const immutableSwap = (items, firstIndex, secondIndex) => {
  const result = [...items];
  [result[firstIndex], result[secondIndex]] = [result[secondIndex], result[firstIndex]];
  return result;
}
export default class extends Controller {

  itemsValue: Array<Object>
  itemsSrcValue: String
  srcKeyValue: String
  itemNameValue: String
  widgetTypeValue: String
  widgetIdValue: String
  maxItemsValue: Number
  itemsTabContainerTarget: HTMLElement
  itemsContainerTarget: HTMLElement
  addButtonTarget: HTMLElement

  static targets = ['itemsTabContainer', 'itemTemplate', 'itemsContainer', 'addButton']

  static values = {
    items: Array,
    itemsSrc: String,
    srcKey: String,
    itemName: String,
    maxItems: { type: Number, default: 0 },
    widgetType: String,
    widgetId: String
  }

  connect():void {
    this._render()
    console.log('kubik_widget_items_controller connected')
    this.itemsSrcValue = this.sourceURL()
  }

  sourceURL():string {
    const widgetController = this.application.getControllerForElementAndIdentifier(document.getElementById('editor-test'), 'editor')
    // @ts-ignore
    return widgetController.widgetsValue[this.srcKeyValue] && widgetController.widgetsValue[this.srcKeyValue]['url'] ? widgetController.widgetsValue[this.srcKeyValue]['url'] : ""
  }


  itemsValueChanged(value, previousValue): void {
    if(value.length != previousValue.length) {
      this._render()
      return
    }
    value.forEach((val, index) => {
      if(val.id !== previousValue[index].id || val.thumb !== previousValue[index].thumb) {
        this._render()
        return
      }
    })
    const otherController = this.application.getControllerForElementAndIdentifier(document.getElementById('editor-test'), 'editor')
    // @ts-ignore
    otherController.saveContents()
  }

  receiveModalReturn(returnObject):void {
    if(typeof returnObject.return_payload.index !== 'undefined') {
      this.itemsValue = [
        ...this.itemsValue.slice(0, returnObject.return_payload.index),
        Object.assign(returnObject.payload, returnObject.return_payload),
        ...this.itemsValue.slice(returnObject.return_payload.index + 1)
      ]
    } else {
      this.itemsValue = [
        ...this.itemsValue,
        Object.assign(returnObject.payload, {})
      ]
    }
  }

  selectResult(e:Event):void {
    const target = e.currentTarget as HTMLElement
    const returnObject = JSON.parse(target.dataset.returnObject)
    const receiveIndex = parseInt(returnObject['receive_index'])
    this.itemsValue = [
      ...this.itemsValue.slice(0, receiveIndex),
      Object.assign(returnObject['return_object'], {}),
      ...this.itemsValue.slice(receiveIndex + 1)
    ]
  }

  moveItemUp(e:Event):void {
    const target = e.currentTarget as HTMLInputElement
    const itemIndex = parseInt(target.dataset.itemIndex)
    if(itemIndex == 0) { return }
    this.itemsValue = immutableSwap(this.itemsValue, itemIndex, itemIndex - 1)
  }

  moveItemDown(e:Event):void {
    const target = e.currentTarget as HTMLInputElement
    const itemIndex = parseInt(target.dataset.itemIndex)
    if(itemIndex == this.itemsValue.length-1) { return }
    this.itemsValue = immutableSwap(this.itemsValue, itemIndex, itemIndex + 1)
  }

  removeItem(e:Event):void {
    const target = e.currentTarget as HTMLInputElement
    const itemIndex = parseInt(target.dataset.itemIndex)
    if(confirm('Are you sure?')) {
      this.itemsValue = [
        ...this.itemsValue.slice(0, itemIndex),
        ...this.itemsValue.slice(itemIndex+1)
      ]
    }
  }

  updateField(e:Event):void {
    const target = e.target as HTMLInputElement
    const value = target.value
    const itemIndex = parseInt(target.dataset.itemIndex)
    const fieldName = target.dataset.kubikName
    const additionalInfo = this.itemsValue[itemIndex]['additional_information'] || {}
    this.itemsValue = [
      ...this.itemsValue.slice(0, itemIndex),
      Object.assign(this.itemsValue[itemIndex], { additional_information: Object.assign( additionalInfo, { [fieldName]: value })}),
      ...this.itemsValue.slice(itemIndex+1)
    ]
  }

  addItem(e:Event):void {
    this.itemsValue = [
      ...this.itemsValue,
      Object.assign({}, { additional_information: {} })
    ]
  }

  canAddMoreItems():boolean {
    return this.maxItemsValue == 0 || this.itemsValue.length < this.maxItemsValue
  }

  renderItem(item:Object, index:Number):string {
    const url = item['url'] || ""
    return `
      <div
        class='kubik-wysiwyg-component--item'
        data-controller='${this.widgetTypeValue}'
        data-${this.widgetTypeValue}-source-widget-value='kubik-widget-items'
        data-${this.widgetTypeValue}-item-index-value='${index}'
        data-${this.widgetTypeValue}-item-id-value='${JSON.stringify(item['id'])}'
        data-${this.widgetTypeValue}-url-value='${url}'
        data-${this.widgetTypeValue}-widget-id-value='${this.widgetIdValue}'
      >
      <div>
    `
  }

  _render(): void {
    this.itemsContainerTarget.innerHTML = ''

    this.itemsValue.forEach((item, index) => {
      this.itemsContainerTarget.insertAdjacentHTML('beforeend', this.renderItem(item, index))
    })

    if (this.canAddMoreItems()) {
      this.addButtonTarget.style.display = 'inline-block'
    } else {
      this.addButtonTarget.style.display = 'none'
    }
  }
}
