import { Controller } from "@hotwired/stimulus"

export default class extends Controller {

  contentValue: Object
  tabContainerTarget: HTMLElement

  static targets = ['tabContainer']

  static values = {
    content: Object
  }

  connect():void {
    console.log('connected content')
  }

  updateWysiwygField(e:Event):void {
    const target = e.target as HTMLElement
    const fieldName = target.dataset.kubikName
    const value = target.innerHTML
    this.contentValue = Object.assign(this.contentValue, { [fieldName]: value })
  }

  updateField(e:Event):void {
    const target = e.target as HTMLInputElement
    const fieldName = target.dataset.kubikName
    const value = target.value
    this.contentValue = Object.assign(this.contentValue, { [fieldName]: value })
  }
}
